*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    // This defines what 1rem is
    font-size: 62.5%;
}

body {
    font-family: 'Jost', sans-serif;
    font-weight: 400;
    box-sizing: border-box;
    /*font-size: 16px;*/
    line-height: 1.7;
    color: $black;
}
a {
    text-decoration:none;
}
p {
    font-size: 1.7rem;
}
h1 {
    font-size: 9.9rem;
}
h2 {
    font-size: 5rem;
}
#wpadminbar {
    position:absolute;
}
@include mediaquery("phone-480"){
    h2 {
        font-size: 4rem;
        line-height: 4.2rem;
    }
}
.container {
    max-width: 1360px;
    @include mediaquery("desktop-1700"){
        max-width: 1200px;
    }
    @include mediaquery("desktop-1440"){
        max-width: 100%;
    }
    margin-left:auto;
    margin-right:auto;
}

